import { inject ,observer} from "mobx-react"
import './history.less'
import DeriNumberFormat from "../../../../utils/DeriNumberFormat"
import dateFormat from 'date-format'
import classNames from "classnames"
import useConfig from "../../../../hooks/useConfig"
import { DeriEnv } from "../../../../lib/web3js"
import Icon from "../../../../components/Icon/Icon"

function History({lang,trading,wallet}){
  const colums = [
    {label : lang['time'],key : 'time' ,format : (value,h) => <a target='_blank' rel='noreferrer' href={`${wallet.blockExploreUrl}/tx/${h.transactionHash}`}>{dateFormat.asString('yyyy-MM-dd hh:mm:ss',new Date(parseInt(value)))}</a>},
    {label : lang['direction'],key : 'direction',format : value => value === 'LIQUIDATION' ? lang['liquidation'] : value === 'SHORT' ? lang['sell'] : lang['buy']},
    {label : lang['symbol'],key : 'symbol'},
    {label : lang['entry-price'],key : 'price',type : 'number'},
    {label : lang['volume'],key : 'volume',type : 'number',decimal : trading.minTradeScaleDecimal},
    {label : lang['notional'],key : 'notional',type : 'number'},
    {label : lang['transaction-fee'],key : 'transactionFee',type : 'number'}
  ]

  return (
    <div className='m-history'>
      <div className='h-header'>
        {colums.map((col,index) => (
          <div key={index} className='h-col'>{col.label}</div>
        ))}
      </div>
      <div className='h-body'>
        <HistoryList loaded={trading.loaded} history={trading.history} colums={colums} lang={lang}/>
      </div>
    </div>
  )
}

function HistoryList({loaded,history ,colums,lang}) {
  return (
    loaded ?
      history.length > 0 
      ? 
      history.map(h => (
        <div className='h-row'>
          {colums.map(col => {
            const value = col.format ? col.format(h[col.key],h) : h[col.key]
            const clazz = classNames('h-col',value)
            return <div className={clazz}>{col.type === 'number' ? <DeriNumberFormat value={value} decimalScale={col.decimal || 2} /> : value}</div>
          })}
        </div>
      ))
      : 
      <div className='no-data'><Icon token='no-data' width='48' height='48' />{lang['no-data']}</div>
    :
    <>
      <div className='h-row loading-line'></div><div className='h-row loading-line'></div><div className='h-row loading-line'></div><div className='h-row loading-line'></div><div className='h-row loading-line'></div>
    </>
  )
}
export default inject('trading','wallet')(observer(History))