import React, { useState,useEffect } from 'react'
import RcSlider,{SliderTooltip} from 'rc-slider'
import 'rc-slider/assets/index.css';
export default function Slider({min,step,max,value,defaultValue,onSlide,className,trackStyle = {},railStyle = {},handleStyle = {}}){
  return (
    <RcSlider
        min={+min}
        max={+max}
        value={+value}
        className={className}
        onChange={onSlide}
        defaultValue={defaultValue}
        trackStyle={{...trackStyle}}
        handleStyle={{...handleStyle}}
        railStyle={{...railStyle}}
        step={(+value) < (+step) ? 0.01 : step}
      />
  )
}