import styled from 'styled-components'
import { inject, observer } from 'mobx-react'
import './contractInfo.less'
import DeriNumberFormat from '../../../utils/DeriNumberFormat'
import Type from '../../../model/Type'
import UnderlineText from '../../../components/UnderlineText/UnderlineText'
import { bg } from '../../../lib/web3js'

const TokenWrapper = styled.span`
  margin-right : 4px;
  display: flex;
`
function ContractInfo({ lang, trading }) {
  const contract = trading.contract || {}
  const basetokenDisplay = (bTokenSymbol = []) => {
    return bTokenSymbol.length === 1
      ? bTokenSymbol[0]
      : <UnderlineText id='btoken' width='375' tip={bTokenSymbol.map((token, index) => `${token} (${contract.bTokenMultiplier[index]})`).join(' ')}>
        {bTokenSymbol.map((token, index) => {
          return (
            index < 3 ? <TokenWrapper key={index}>{token}({contract.bTokenMultiplier[index]})</TokenWrapper> : null
          )
        })}...
          </UnderlineText>
  }
  let rows = []
  if (Type.isFuture) {
    rows = [
      { label: lang['base-token'], labelDes: lang['discount-factor'], tip: lang['basetoken-multiplier-tip'], key: 'baseToken', value: basetokenDisplay(contract.bTokenSymbol) },
      { label: lang['symbol'], key: 'symbol', value: contract['symbol'] },
      { label: lang['min-trade-volume'], key: 'multiplier', value: trading.contract['multiplier'] ? trading.contract['multiplier'] + trading.symbolInfo.unit : '' },
      { label: lang['funding-rate-coefficent'], key: 'fundingRateCoefficient', value: (contract['fundingRateCoefficient'] && (+contract['fundingRateCoefficient']).toExponential()), valueTip: trading.fundingCoefficientTip, tipWidth: 375 },
      { label: lang['initial-margin-ratio'], key: 'minInitialMarginRatio', tip: lang['initial-margin-ratio-tip'], value: <DeriNumberFormat value={contract['initialMarginRatio'] * 100} decimalScale={0} suffix='%' /> },
      { label: lang['maintenance-margin-ratio'], key: 'minMaintenanceMarginRatio', tip: lang['maintenance-margin-ratio-tip'], value: <DeriNumberFormat value={contract['maintenanceMarginRatio'] * 100} decimalScale={0} suffix='%' /> },
      { label: lang['transaction-fee'], key: 'feeRatio', value: <DeriNumberFormat value={contract['feeRatio'] * 100} suffix='%' /> },
    ]
  } else {
    rows = [
      { label: lang['base-token'], labelDes: lang['discount-factor'], tip: lang['basetoken-multiplier-tip'], key: 'baseToken', value: basetokenDisplay(contract.bTokenSymbol) },
      { label: lang['symbol'], key: 'symbol', value: contract['symbol'] },
      { label: lang['underlier'], key: 'symbol', value: contract['underlier'] },
      { label: lang['strike'], key: 'symbol', value: contract['strike'] },
      { label: lang['option-type'], key: 'symbol', value: contract['optionType'] },
      { label: lang['min-trade-volume'], key: '', value: trading.contract['multiplier'] ? trading.contract['multiplier'] + trading.symbolInfo.unit : '' },
      {
        label: lang['initial-margin-ratio'], tip: `${lang['initial-margin-ratio-tip']} <br> ${lang['option-initial-margin-ratio-formula']} ${bg(contract['initialMarginRatio']).times(100).toFixed(2)}%`, key: 'minInitialMarginRatio',
        value: <DeriNumberFormat value={contract['initialMarginRatio'] * 100} decimalScale={2} suffix='%' />
      },
      {
        label: lang['maintenance-margin-ratio'], tip: (() => {
          return `${lang['maintenance-margin-ratio-tip']} <br> ${lang['maintenance-margin-ratio-1']} ${bg(contract['maintenanceMarginRatio']).times(100).toFixed(2)}% <br> ${lang['maintenance-margin-ratio-2']} ${trading.index} * 4% = ${bg(trading.index).times(0.04)} `
        })(), tipHtml: true, key: 'maintenanceMarginRatio',
        element: <span className='tip-width'>
        <div>{lang['maintenance-margin-ratio-tip']}</div>
          {(() => {
            return` ${lang['maintenance-margin-ratio-1']} ${bg(contract['maintenanceMarginRatio']).times(100).toFixed(2)}%  ${lang['maintenance-margin-ratio-2']} ${trading.index} * 4% = ${bg(trading.index).times(0.04)} `
          })()}
          <div><a target="_blank" rel='noreferrer' href='https://deri-protocol.medium.com/changes-to-everlasting-options-81e446768559'> {lang['learn-more']}</a></div></span>,
        value: <DeriNumberFormat value={contract['maintenanceMarginRatio'] * 100} decimalScale={2} suffix='%' />
      },
      { label: lang['transaction-fee'], tip: trading.TransactionFeeTip, tipWidth: '320', key: 'feeRatio', value: `${trading.feeDisplay.join(' ')}%` },
      { label: lang['funding-period'], key: 'feeRatio', value: <DeriNumberFormat value={bg(contract['fundingPeriod']).div(86400).toFixed(0)} suffix={` ${lang['days']}`} />, tip: lang['funding-period-tip'], tipHtml: true, element: <span className='tip-width'><div>{lang['funding-period-tip']}</div> {lang['funding-period-tip-two']} <div>
        {/* <a target="_blank" rel='noreferrer' href='https://deri-protocol.medium.com/changes-to-everlasting-options-81e446768559'> {lang['learn-more']}</a> */}
      </div></span>, },
      { label: lang['delta'], key: 'delta', value: <DeriNumberFormat value={contract['delta']} decimalScale='2' /> },
      { label: lang['gamma'], key: 'gamma', value: <DeriNumberFormat value={contract['gamma']} decimalScale='6' /> },
    ]
  }
  return (
    <div className='contract-info'>
      <div className='c-title'>{lang['contract-info']}</div>
      <div className='c-body'>
        {rows.map((row, index) => (
          row.value && <div className='c-row' key={index}>
            <div>
              {row.tip && !row.labelDes ? <UnderlineText id={row.label} html={row.tipHtml} element={row.element} text={row.label} multiline={true} tip={row.tip} width={row.tipWidth} /> : row.labelDes ? <span className='base'>{row.label} (<UnderlineText text={row.labelDes} multiline={true} block='inline-block' id={row.label} tip={row.tip} width={row.tipWidth} />) </span> : row.label}
            </div>
            <div className='c-value'>
              {row.valueTip ? <UnderlineText text={row.value} multiline={true} tip={row.valueTip} width={row.tipWidth} /> : row.value}
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
export default inject('trading')(observer(ContractInfo))